"use client";

import { Formik, Form, Field } from "formik";
import { useRef, useState } from "react";

export default function Test(){

     const bannerRef = useRef(null);
     const [preview, setPreview] = useState(null);
       const [fileName, setFileName] = useState("");
    return(
        <>
        <Formik
      initialValues={{ banner: null }}
      validate={(values) => {
        const errors = {};
        if (!values.banner) {
          errors.banner = "Banner is required";
        }
        return errors;
      }}
      onSubmit={(values) => {
        console.log("Submitted:", values);
      }}
    >
      {({ errors, values, touched, setFieldValue,validateForm,isValid }) => (
        <Form>
          <div className="sm:col-span-full">
                <div className="flex justify-center">
                  {errors.banner && touched.banner && (
                    <p className="text-red-500 text-sm mt-2 mx-5">
                      {errors.banner}
                    </p>
                  )}
                  <div className="w-[30%]">
                    <input
                      ref={bannerRef}
                      type="file"
                      id="banner"
                      name="banner"
                      accept="image/*"
                      onChange={(e) => {
                        const file = e.target.files[0];
                        if (file) {
                          setFieldValue("banner", file);
                          setFileName(file.name);

                          const reader = new FileReader();
                          reader.readAsDataURL(file);
                          reader.onloadend = () => {
                            setPreview(reader.result);
                          };
                        }
                      }}
                      className="hidden"
                    />
                    <label
                      id="banner-label"
                      htmlFor="banner"
                      className={`flex bg-white border-2 border-gray-400 px-4 py-2 rounded-md text-gray-400 text-center cursor-pointer hover:bg-gray-100
                      ${
                        errors.banner && touched.banner
                        ? "border-red-500"
                        : ""
                        }`}
                    >
                      <img
                        src="/upload.png"
                        alt="Upload Icon"
                        className="w-5 h-5 mr-3"
                      />
                      Upload Banner
                    </label>
                  </div>
                </div>
                <div className="sm:col-span-full mt-5">
                  {preview && (
                    <img
                      src={preview}
                      alt="Banner Preview"
                      className="w-full h-48 object-cover"
                    />
                  )}
                  {fileName && (
                    <p className="mt-2 text-gray-700 text-sm text-center">
                      {fileName}
                    </p>
                  )}
                </div>
            </div>
            <div className="sm:col-span-full">
                <div className="px-4 flex justify-end">
                  <button
                    type="reset"
                    className="justify-center mx-2 rounded-md bg-white px-3 py-2 text-sm font-semibold shadow-xs ring-gray-300 ring-inset hover:bg-gray-300 text-(color:--my1)  sm:w-auto"
                  >
                    Cancel
                  </button>
                  <button
                    type="submit"
                    disabled={!isValid}
                    onClick={() => {
                        validateForm;
                    }}
                    className={`justify-center ml-2 rounded-md bg-white px-3 py-2 text-sm font-semibold text-gray-900 shadow-xs ring-gray-300 ring-inset sm:w-auto ${
                        !isValid
                        ? "cursor-not-allowed"
                        : "hover:bg-gray-300 cursor-pointer"
                    }`}
                    
                    >
                    Add
                  </button>
                </div>
              </div>
        </Form>
        )}

    </Formik>
    </>
        
  );
}